		// 获取页面内信息
		async getCarInfo() {
			const opts = {
				url: "/sc/carMng/getCanUseCars",
				method: "post"
			}

			const res = await this.$http.httpTokenRequest(opts);
			this.infoList = res.data.result;
			this.infoList.map((item, index) => {
				this.carList.push(item.carNo);
			})
			this.infoList.map((item, index) => {
				this.carIdList.push(item.carId);
			})
		},

		async getPlanInfo() {
